import $ from 'jquery/dist/jquery.min.js';
import scrollLock from "scroll-lock/dist/scroll-lock.min.js";

export default () => {
	if (window.matchMedia('(max-width: 1024px)').matches) {
		const mobileMenu = new MobileMenu();
	}
}

class MobileMenu {
	constructor() {
		this.$root = null;
		this.$trigger = null;

		this.opened = false;

		this.init();
	}

	init() {
		this.$root = $('#mobileMenu');

		if (!this.$root) return;

		this.$trigger = $('#mobileMenuTrigger');

		this.addEventListeners();
	}

	addEventListeners() {
		this.$trigger.click(() => {
			if (this.opened) {
				this.close();
			} else {
				this.open();
			}
		});
	}

	open() {
		this.opened = true;
		this.$root.fadeIn(250);
		this.$trigger.addClass('-active');
		scrollLock.disablePageScroll(this.$root[0]);
	}

	close() {
		this.opened = false;
		this.$root.fadeOut(250);
		this.$trigger.removeClass('-active');
		scrollLock.enablePageScroll(this.$root[0]);
	}
}